::selection {
  background: none;
}
.body-p {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  color: #111c31;
  font-family: arial;
  background-color: #cecece;
  font-size: 12px;
}
.pagination {
  font-size: 0.9rem;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.pagination .pagination-item {
  text-decoration: none;
  position: relative;
  margin: 0 10px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3),
    inset 0 -5px 0px rgba(80, 80, 80, 0.1),
    inset 0 -10px 0px rgba(150, 150, 150, 0.1);
  overflow: hidden;
  color: #093170;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  background-color: #efefef;
}
.pagination .pagination-item:before,
.pagination .pagination-item:after {
  content: "";
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 11;
  background-image: linear-gradient(135deg, #9b1301, #5eea00);
  transform: translateX(-100px);
  transition: 0.3s ease-in-out;
}
.pagination .pagination-item:after {
  content: "click";
  color: #fff;
  background-image: linear-gradient(135deg, #702f09, #7dbbeb);
  transition-delay: 0.25s;
  transform: translateY(100px);
}
.pagination .pagination-item:hover {
  box-shadow: 0 0px 20px rgba(0, 0, 0, 0.4),
    inset 0 -5px 0px rgba(80, 80, 80, 0),
    inset 0 -10px 0px rgba(150, 150, 150, 0);
}
.pagination .pagination-item:hover:before,
.pagination .pagination-item:hover:after {
  transform: translateY(0);
}
.pagination .pagination-item:active {
  box-shadow: none;
}
.pagination .pagination-item.active {
  font-size: 0.8rem;
  color: #fff;
  background-image: linear-gradient(135deg, #008094, #3ec708);
}
